.notiContainer {
    position: fixed;
    z-index: 1000;
    width: 0 auto;
    bottom: 30px;
    margin: 0 auto;
    left: 30px;
    right: 30px;
    display: flex;
    flex-direction: column;
    pointer-events: none;
    align-items: flex-end;
    @media (max-width: 680px) {
        align-items: center;
    }
    .message {
        box-sizing: border-box;
        position: relative;
        overflow: hidden;
        width: 40ch;
        @media (max-width: 680px) {
            width: 100%;
        }
        .messageContent {
            color: white;
            background: #445159;
            opacity: 0.9;
            padding: 5px 22px 12px;
            font-size: 1em;
            display: grid;
            grid-template-columns: 1fr auto;
            grid-gap: 10px;
            overflow: hidden;
            height: auto;
            border-radius: 5px 5px 0 0;
            margin-top: 10px;
            .life {
                position: absolute;
                bottom: 0;
                left: 0px;
                width: auto;
                background-image: linear-gradient(130deg, #00b4e6, #00f0e0);
                height: 5px;
            }
            button {
                cursor: pointer;
                pointer-events: all;
                outline: 0;
                border: none;
                background: transparent;
                display: flex;
                align-self: center;
                overflow: hidden;
                margin: 0;
                padding: 0;
                color: rgba(255, 255, 255, 0.5);
                :hover {
                    color: rgba(255, 255, 255, 0.8);
                }
                svg {
                    width: 20px;
                }
            }
        }
    }
}
