.introduction{max-width:1300px;margin:0 auto;padding:3rem;padding-bottom:15rem}.introduction .inner{margin:0;padding:0;display:flex;justify-content:center;align-items:center;width:100%}.introduction .inner .innerContent{width:100%}.introduction .inner .innerContent .holder{margin:50px auto;background:#fae7e6;border-radius:15px;padding:20px;padding-bottom:30px;width:100%;display:flex;flex-direction:column;border:1px solid salmon;box-shadow:rgba(250,146,134,.35) 0px 20px 25px -5px,rgba(250,146,134,.51) 0px 10px 10px -5px}.introduction .inner .innerContent .holder .introicon{margin-bottom:20px;display:flex;flex-direction:row;justify-content:flex-end;width:100%;gap:15px}.introduction .inner .innerContent .holder .introicon svg{cursor:pointer;color:#fa7667}.introduction .inner .innerContent .holder .noicon{display:flex;flex-direction:row;gap:20px;width:100%}.introduction .inner .innerContent .holder .noicon .ntroleft{width:50%;display:flex;justify-content:center;align-items:center}.introduction .inner .innerContent .holder .noicon .ntroleft img{width:100%;border-radius:10px;box-shadow:rgba(0,0,0,.12) 0px 1px 3px,rgba(0,0,0,.24) 0px 1px 2px}.introduction .inner .innerContent .holder .noicon .ntroright{width:50%;display:flex;flex-direction:column;justify-content:space-between;align-items:center;gap:15px;text-align:center}.introduction .inner .innerContent .holder .noicon .ntroright h3{color:#fa7667}.introduction .inner .innerContent .holder .noicon .ntroright p{text-align:justify}.introduction .inner .innerContent .holder .noicon .ntroright .aboutRanking{width:100%;height:50%;padding:0 20px;border-radius:10px;background-color:#fff7f8;box-shadow:rgba(250,146,134,.5) 0px 1px 4px}.introduction .inner .innerContent .holder .noicon .ntroright .aboutOrganizer{width:100%;height:50%;padding:0 20px;border-radius:10px;background-color:#fff7f8;box-shadow:rgba(250,146,134,.5) 0px 1px 4px}@media screen and (max-width: 750px){.introduction .inner .innerContent .holder .introicon{justify-content:center}.introduction .inner .innerContent .holder .noicon{flex-direction:column}.introduction .inner .innerContent .holder .noicon .ntroleft{width:100%}.introduction .inner .innerContent .holder .noicon .ntroright{width:100%}.introduction .inner .innerContent .holder .noicon .ntroright h3{font-size:1.4rem}.introduction .inner .innerContent .holder .noicon .ntroright p{font-size:1.3rem}}